@mixin heading {
    font-family: $font-heading;
    font-weight: 700;
    color: rgba($color-white, 0.9);
}

@mixin heading-small {
    font-family: $font-heading;
    font-weight: 600;
    color: $color-white;
    font-size: 2rem;
    line-height: 3rem;
}

@mixin text {
    font-family: $font-text;
    font-weight: 400;
    color: $color-text;
    font-weight: 400;
    line-height: 2.8rem;
}

@mixin button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 16.4rem;
    height: 4.8rem;
    background-color: $color-accent;
    color: $color-text-dark;
    cursor: pointer;
}

@mixin read-link {
    display: flex;
    align-items: center; 
    width: 16.4rem;
    color: $color-white;
    cursor: pointer;
}

@mixin hover-read-link {
    color: $color-white;
    transform: scale(1.05);
     transition: all 0.3s;
    filter: drop-shadow(0 0.5rem 1rem $color-accent);
}

@mixin section-header {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

@mixin hover-link {
    color: $color-accent;
    transform: scale(1.3);
    transition: all 0.3s;
    filter: drop-shadow(0 0.5rem 1rem $color-accent);
}

@mixin image-container {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    

     img{
        width: 100%;
        height: auto;
        object-fit: contain;
 
    }
}

@mixin respond($breakpoint) {
    //600px
    @if $breakpoint == phone {
        @media only screen and (max-width: 37.5em) {
            @content;
        }
    }
    //900px
    @if $breakpoint == tab-port {
        @media only screen and (max-width: 56.25em) {
            @content;
        }
    }
    //1200px
    @if $breakpoint == tab-land {
        @media only screen and (max-width: 75em) {
            @content;
        }
    }
    //1800--
    @if $breakpoint == pig-desktop {
        @media only screen and (min-width: 112.5em) {
            @content;
        }
    }
}